import React from "react";
import { useState, useEffect, useRef } from "react";
import { useSelector } from "react-redux";
import * as d3 from "d3";

export default function Table(props){
    const data = useSelector((state) => state.data);
    const uploadedFiles = useSelector((state) => state.uploadedFiles);
    const colNames = useSelector((state) => state.colNames);
    const kWords = useSelector((state) => state.kWords);
    const dTypes = useSelector((state) => state.dTypes);
    const colSummary = useSelector((state) => state.colSummary);

    // modal
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [selectedFile, setSelectedFile] = useState(null);
    const [selectedFileID, setSelectedFileID] = useState(null);

    //chart
    // const ref = useRef();

    // console.log(colNames)
    // console.log(kWords)
    // console.log(dTypes)
    // console.log(colSummary)
    // console.log(data)

    if (!data) {
        // Data is not loaded yet, render a loading message or nothing at all
        return <div>Loading...</div>;
      }
      
    const openModal = (file, id) => {
        setSelectedFile(file);
        setSelectedFileID(id);
        setIsModalOpen(true);
    }

    const closeModal = () => {
        setIsModalOpen(false);
        setSelectedFile(null);
        setSelectedFileID(null);
    }

    // useEffect(() => {
    //     console.log(data[selectedFileID])
    //     console.log(colID)
    //     console.log(colDType)
    // })

    const barChart = (selection, data, selectedFileID, colIndex) => {
        const numofUniqueID = colSummary[selectedFileID][colIndex].numUnique
        const columnDataWithHead = data.map(row => row[colIndex]);
        const columnData = columnDataWithHead.slice(1)
        // console.log(columnData)
        let uniqueTextCounts = columnData.reduce((counts, text) => {
            counts[text] = (counts[text] || 0) + 1;
            return counts;
        }, {})

        //Prepare the data for d3
        const _d3Data = Object.keys(uniqueTextCounts).map(text => ({
            text,
            count: uniqueTextCounts[text]
        }));

        _d3Data.sort((a, b) => b.count - a.count);
        let d3Data = _d3Data.slice(0, Math.min(50, _d3Data.length)); // Ensuring not to exceed the array length

        // console.log(columnData)
        // console.log(dTypes)
        const margin = {top: 10, right: 10, bottom: 80, left: 40};
        const padding = 0.2;
        const width = 150 - margin.left - margin.right;
        const height = 200 - margin.top - margin.bottom;

        const svg = selection
            .append("svg")
            .attr("width", width + margin.left + margin.right)
            .attr("height", height + margin.top + margin.bottom)
            .append("g")
            .attr("transform", `translate(${margin.left},${margin.top})`);

        // Create scales
        const x = d3.scaleBand()
            .range([0, width])
            .domain(d3Data.map((d) => d.text)) // Use index as the domain
            .padding(padding);

        const y = d3.scaleLinear()
            .range([height, 0])
            .domain([0, d3.max(d3Data, d => d.count*1.2)]); // Use maximum data value as the domain

        svg.selectAll("rect")
            .data(d3Data)
            .enter()
            .append("rect")
            .attr("x", d => x(d.text)+padding)
            .attr("width", x.bandwidth())
            .attr("y", d => y(d.count))
            .attr("height", d => height - y(d.count))
            .attr("fill", "#69b3a2")

        svg.append("g")
            .attr("transform", `translate(0,${height})`)
            .call(d3.axisBottom(x))
        
        const tickVisibility = (d, i) => (numofUniqueID < 20 || i === 1) ? "visible" : "hidden";

        svg.selectAll("text")
            .text(d => d)
            .style("visibility", tickVisibility)
            .attr("class", "sm_chart_label")
            .attr("transform", "rotate(-45)")  // Rotate the text
            .style("text-anchor", "end");
    
        svg.selectAll(".tick line")
            .each(function(d, i){
                if (numofUniqueID >= 20){
                    d3.select(this).remove()
                }
            })

        svg.append("g")
            .call(d3.axisLeft(y))
    }

   
  return (
    <div>
        {/* Table */}
            <div className="tableContainer table-text-size">
            {data.map((tableData, i) => (
                <div className="data-table-wrapper" key={i}>
                    <button className="modal-btn" onClick={() => openModal(uploadedFiles[i], i)}>
                        Open {uploadedFiles[i]?.name} in modal
                    </button>
                        <div className="data-table">
                            <div className="white-rect">
                                <div className="textbox summary">
                                    <span>All Columns</span>
                                    <div className="colnames">
                                        {colNames[i]?.map((word, index) => (
                                            <span key={index} className="textTag">{word}</span>
                                        ))}
                                    </div>    
                                    <span>Most Frequent Texts</span>
                                    <div className="kwords">
                                        {kWords[i]?.map((word, index) => (
                                            <span key={index} className="textTag">{word}</span>
                                        ))}
                                    </div>
                                </div>
                            </div> 
                                <div className="table-frame">
                                    <table>
                                        <thead>
                                            <tr>
                                                {tableData[0].map((header, index) => (
                                                    <th key={index}>{header}</th>
                                                ))}
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {tableData.slice(1).map((row, j) => (
                                                <tr key={j}>
                                                    {row.map((value, k) => (
                                                        <td key={k}>{value}</td>
                                                    ))}
                                                </tr>
                                            ))}
                                        </tbody>
                                    </table>
                                </div>
                        </div>
                </div>
            ))}
            </div>

            {isModalOpen && (
            <div className="modal">
                <div className="modal-content">
                    <span className="close-button" onClick={closeModal}>
                        &times;
                    </span>

                    <div className="selectedTableContainer table-text-size">
                        <h3>{selectedFile && <p>{selectedFile.name}</p>}</h3>
                            <div className="selectedTable">
                                <table className="data-table">
                                    <thead>
                                       <tr className="summary-col">
                                          {/* {barChart(data[selectedFileID])} */}
                                          {data[selectedFileID][0].map((header, index) => {
                                                const selectedData = colSummary[selectedFileID];
                                                const columnStats = selectedData[index]
                                                const dataType = dTypes[selectedFileID][index];
                                                // console.log(columnStats)
                                                // console.log(dataType)
                                                // console.log(index)
                                                // setColDType(dataType)
                                                // setColID(index)
                                                if (dataType === 'number'){
                                                    return(
                                                        <th key={index}>
                                                            Mean: {columnStats && typeof columnStats.mean === 'number' ? columnStats.mean.toFixed(2) : 'N/A'} <br/>
                                                            Median: {columnStats && typeof columnStats.median === 'number' ? columnStats.median.toFixed(2) : 'N/A'} <br/>
                                                            Min: {columnStats && typeof columnStats.min === 'number' ? columnStats.min.toFixed(2) : 'N/A'} <br/>
                                                            Max: {columnStats && typeof columnStats.max === 'number' ? columnStats.max.toFixed(2) : 'N/A'} <br/>
                                                            Stddev: {columnStats && typeof columnStats.stddev === 'number' ? columnStats.stddev.toFixed(2) : 'N/A'} <br/>
                                                        </th>
                                                    )
                                                } else if (dataType === 'string'){
                                                    return(
                                                        <th key={index}>
                                                            <div
                                                                ref={el => d3.select(el).call(barChart, data[selectedFileID], selectedFileID, index)}
                                                                className="bar-chart"
                                                            ></div>
                                                            Num of Unique Texts: <br/>{columnStats && columnStats.numUnique !== undefined ? columnStats.numUnique : 'N/A'} <br/>
                                                            Most Frequent Text: <br/>{columnStats && columnStats.mostFrequent !== undefined ? columnStats.mostFrequent : 'N/A'} <br/>
                                                        </th>
                                                    )
                                                }
                                            })}
                                            </tr>
                                        <tr>
                                            {data[selectedFileID][0].map((header, index) => (
                                                <th key={index}>{header}</th>
                                            ))}
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {data[selectedFileID].slice(1).map((row, j) => (
                                            <tr key={j}>
                                                {row.map((value, k) => (
                                                    <td key={k}>{value}</td>
                                                ))}
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>
                            </div>
                    </div>
                </div>
            </div>
            )}
    </div>
  );
};