import {createStore} from 'redux';
export default createStore(function(state, action){
    // console.log(state)
    if(state === undefined){
        return {
            uploadedFiles: {},
            data: [],
            colNames: [],
            kWords: [],
            dTypes: [],
            colSummary: []
        }
    }
    if(action.type === 'SUBMIT'){
        return {...state, uploadedFiles: action.uploadedFiles}
    }
    if(action.type === 'DATA'){
        return {...state, data: action.data}
    }
    if(action.type === 'COLNAMES'){
        return {...state, colNames: action.colNames}
    }
    if(action.type === 'KEYWORDS'){
        return {...state, kWords: action.kWords}
    }
    if(action.type === 'DATATYPES'){
        return {...state, dTypes: action.dTypes}
    } 
    if(action.type === 'COLSUMMARY'){
        return {...state, colSummary: action.colSummary}
    }
    return state;
}, window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__())