import { useState, useEffect } from "react";
import Papa from 'papaparse';
import * as dfd from "danfojs";
import _ from "lodash";
import nlp from 'compromise';

export default function FileAdd(props) {
    const [csvFiles, setCsvFiles] = useState([]);
    const [data, setData] = useState([]);
    const [fileNames, setFileNames] = useState([]); // To store file names of new files
    const [submittedFileNames, setSubmittedFileNames] = useState([]); // To store file names of submitted files

    useEffect(() => {
        props.onData(data)
        props.onColNames(colNamesHandler())
    });

    const colNamesHandler = () => {
        if (data && data.length > 0) {
           return Array.from(data, col => col[0])
        }
    }

    const onDrop = (e) => {
        e.preventDefault();
        let newFileNames = [];
        let newFiles = []; 
        if (e.dataTransfer.items) {
            for (var i = 0; i < e.dataTransfer.items.length; i++) {
                if (e.dataTransfer.items[i].kind === 'file') {
                    var file = e.dataTransfer.items[i].getAsFile();
                    newFiles.push(file); 
                    newFileNames.push(file.name);
                }
            }
        }
        setCsvFiles(newFiles);
        setFileNames(newFileNames); // Set the file names of the new files.
    }

    const dragOver = (e) => {
        e.preventDefault();
    }

    const checkDataTypes = (data) => {
        const dataTypes = {};
        const firstRow = data[1];

        for (let key in firstRow) {
            let value = firstRow[key];
            let type = getType(value);
            dataTypes[key] = type;
        }
        return dataTypes
    }

    const getType = (value) => {
        if (isNaN(value)){
            return 'string';
        } else {
            return 'number';
        }
    }

    // Helper function to compute the median
    const median = (arr) => {
        const mid = Math.floor(arr.length / 2);
        const nums = [...arr].sort((a, b) => a - b);
        return arr.length % 2 !== 0 ? nums[mid] : (nums[mid - 1] + nums[mid]) / 2;
    };

    // Helper function to compute the standard deviation
    const stddev = (arr, arrMean) => {
        return Math.sqrt(_.sum((arr.map((num) => Math.pow(num - arrMean, 2)))) / arr.length);
    };

    const getColSummary = (data, dataTypes) => {
        const summary = {};
        
        // Transpose the data so we can work with columns
        const columns = Object.keys(data[0]).map(colName => data.map(row => row[colName]))

        columns.forEach((column, i) => {
            const colName = Object.keys(data[0])[i];
            const colType = dataTypes[colName];

            // Create an array of numbers for the column if it's numeric
            const numericColumn = colType === 'number' ? column.filter(val => !isNaN(val)) : [];

            if (colType === 'number' && numericColumn.length > 0) {
                const colMean = _.mean(numericColumn);
                // Compute numerical summaries
                summary[colName] = {
                    mean: colMean,
                    median: median(numericColumn),
                    min: _.min(numericColumn),
                    max: _.max(numericColumn),
                    stddev: stddev(numericColumn, colMean)
                };
            } else {
              // Compute string summaries
              const uniquevalues = _.uniq(column);
              const valueCounts = _.countBy(column);

              summary[colName] = {
                numUnique: uniquevalues.length,
                mostFrequent: _.maxBy(Object.keys(valueCounts), value => valueCounts[value])
                // longestString: _.maxBy(column, value => value.length)
              }
            }
        })

        return summary
    }

    const getFrequentWords = (data) => {
        const columnTexts = {};
        const columnFrequentWords = {};

        data.forEach(row => {
            Object.entries(row).forEach(([columnName, value]) => {
                if(typeof value === 'string'){
                    if(!columnTexts[columnName]) columnTexts[columnName] = '';
                    columnTexts[columnName] += ' ' + value;
                }
            });
        });

        Object.entries(columnTexts).forEach(([columnName, text]) => {
            const frequencyCounts = _.countBy(text.split(' '));
            const mostFrequentWords = _.keys(frequencyCounts).sort((a, b) => frequencyCounts[b] - frequencyCounts[a]).slice(2, 3);
            columnFrequentWords[columnName] = mostFrequentWords;
        });

        return columnFrequentWords;
    }

    const changeHandler = () => {
        setData([]);
        // let allKeywords = [];
        let words = [];
        let keyWordsArr = [];
        let dataTypesArr = [];
        let colSummaryArr = [];
        let accumulatedData = [];

        Array.from(csvFiles).forEach((file, i) => {
            Papa.parse(file, {
                header: false,
                dynamicTyping: true,
                chunk: function(results) {
                    // accumulatedData.push(...results.data)
                    setData(oldData => [...oldData, results.data]);
     
                    //allKeywords[i] = getFrequentWords(results.data);
                    //Compute Keywords
                    const columnFrequentWords = getFrequentWords(results.data);
                    //Compute datatypes of each col in all csv
                    const dTypes = checkDataTypes(results.data);
                        dataTypesArr.push(dTypes)
                    //Compute summary data of each col of each file
                    const colSummary = getColSummary(results.data, dTypes)
                        // console.log(colSummary);  // or do something with colSummary
                        colSummaryArr.push(colSummary)

                    words = Array.from(Object.entries(columnFrequentWords), (([key, arr]) => arr[0]))
                    words = words.filter(item => item !== undefined && item.trim() !=="" && !item.startsWith('http'));
                    keyWordsArr.push(words)
                },
                complete: function(results) {
                    console.log("All chunks parsed"); 
                   
                    // console.log(dataTypesArr)
                }
            });
        });
        props.onSubmit(csvFiles)
        props.onKeyWords(keyWordsArr)
        props.onDataTypes(dataTypesArr)
        props.onColSummary(colSummaryArr)
        setSubmittedFileNames(fileNames); // Save the file names of the new files as submitted files
        setCsvFiles([]);
        setFileNames([]); // Clear the new file names
    };

    return (
        <div>
            <div className="flex_horizontal">
                <div id="dropzone" className="black_border" onDrop={onDrop} onDragOver={dragOver}>
                    <b>Drag 'n' drop CSV files here</b>
                    <p>Number of files: {fileNames.length > 0 ? fileNames.length : submittedFileNames.length}</p>
                    <ul>
                        {(fileNames.length > 0 ? fileNames : submittedFileNames).map((name, index) => (
                            <li key={index}>{name}</li>
                        ))}
                    </ul>
                    <button id="submit" type="submit" onClick={changeHandler}>Submit</button>
                </div>
            </div>
            <div className="height-10"></div>
        </div>
    )
}
