import FileAdd from '../components/FileAdd';
import {connect} from 'react-redux';
function mapReduxStateToReactProps(state){
    return {
        // uploadedFiles:state.uploadedFiles,
        // data:state.data
    }
}
function mapDispatchToProps(dispatch){
    return {
        onSubmit:function(uploadedFiles){
            dispatch({type:'SUBMIT', uploadedFiles:uploadedFiles});
        },
        onData:function(data){
            dispatch({type:'DATA', data:data});
        },
        onColNames:function(colNames){
            dispatch({type:'COLNAMES', colNames:colNames});
        },
        onKeyWords:function(kWords){
            dispatch({type:'KEYWORDS', kWords:kWords});
        },
        onDataTypes:function(dTypes){
            dispatch({type:'DATATYPES', dTypes:dTypes})
        },
        onColSummary:function(colSummary){
            dispatch({type:'COLSUMMARY', colSummary:colSummary})
        }
    }
}
export default connect(mapReduxStateToReactProps, mapDispatchToProps)(FileAdd);